import React, { useContext, useState } from "react";
import SingInformBody from "./SingInformBody";
import { useNavigate } from "react-router-dom";
import Data from "../../../../Api";
import PhotoData from "../../../../ApiPhoto";
import { useEffect } from "react";
import { Context } from "../../../../Context/Context";
import Cookies from "js-cookie";
import { Alerts } from "../../../../Context/Alerts";

const SignInMain = () => {
  const [user, setUser] = useContext(Context);

  const [alert, setAlert] = useContext(Alerts);

  const [firstName, setFirst] = useState("");

  const [lastName, setLast] = useState("");

  const [email, setEmail] = useState("");

  const [age, setAge] = useState(0);

  const [password, setPassword] = useState("");

  const [confirmPassword, setConfirmPassword] = useState("");

  const [role_id, setRole] = useState(0);

  const [picture, setPicture] = useState("");

  const [err, setErr] = useState([]);

  const validEmail = new RegExp(
    "^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$"
  );

  const validPassword = new RegExp("^([a-zA-Z0-9@*#]{6,20})$");

  const validName = new RegExp("^([a-zA-Z.s']{1,50})$");

  const Api = new Data();

  const [id, setId] = useState(0);

  const navigate = useNavigate();

  const randomId = () => {
    let id = Math.floor(Math.random() * 999) + 1;

    setId(id);
  };

  const handlechanger = (
    firstName,
    lastName,
    email,
    age,
    password,
    confirmPassword,
    role_id,
    picture
  ) => {
    setFirst(firstName);
    setLast(lastName);
    setEmail(email);
    setAge(age);
    setPassword(password);
    setConfirmPassword(confirmPassword);
    setRole(role_id);
    setPicture(picture);
  };

  const check = () => {
    setErr([]);

    if (firstName == "") {
      setErr((prev) => {
        return [...prev, "First Name is required"];
      });
    } else if (!validName.test(firstName)) {
      setErr((prev) => {
        return [...prev, "The name is not valid"];
      });
    }

    if (lastName == "") {
      setErr((prev) => {
        return [...prev, "Last name is required"];
      });
    } else if (!validName.test(lastName)) {
      setErr((prev) => {
        return [...prev, "The name is not valid"];
      });
    }

    if (email == "") {
      setErr((prev) => {
        return [...prev, "Email is required"];
      });
    } else if (!validEmail.test(email)) {
      setErr((prev) => {
        return [...prev, "The email adress is not valid!"];
      });
    }

    if (age == "") {
      setErr((prev) => {
        return [...prev, "Age is required"];
      });
    } else if (age > 80 || age < 18) {
      setErr((prev) => {
        return [...prev, "Age is not valid"];
      });
    }

    if (password == "") {
      setErr((prev) => {
        return [...prev, "Password is required"];
      });
    } else if (!validPassword.test(password)) {
      setErr((prev) => {
        return [...prev, "The password must be between 6-20 characters"];
      });
    }

    if (confirmPassword == "") {
      setErr((prev) => {
        return [...prev, "Plase confirm your password"];
      });
    }

    if (password && confirmPassword) {
      if (password != confirmPassword) {
        setErr((prev) => {
          return [...prev, "Passwords must match!"];
        });
      }
    }

    if (role_id == 0) {
      setErr((prev) => {
        return [...prev, "You need to select a role !"];
      });
    }
  };

  const signStudent = async () => {
    check();

    if (err == "") {
      let student = await Api.addStudent({
        id,
        firstName,
        lastName,
        email,
        age,
        password,
        role_id,
      });
      let photoData = new PhotoData();

      console.log(student);

      await photoData.uploadPhoto(student.id, picture);

      if (typeof student == "object") {
        setUser(student);
        setAlert("Accout created succesfuly");
        Cookies.set("authentificatedUser", JSON.stringify(student));
        navigate("/");
      } else {
        setErr((prev) => {
          console.log(prev);
          return [...prev, student];
        });
      }
    }

    // console.log(err);
  };

  useEffect(() => {
    randomId();
  }, []);

  useEffect(() => {
    check();
  }, [firstName, lastName, age, email, password, confirmPassword]);

  return (
    <>
      <section className="form">
        <SingInformBody
          handlechanger={handlechanger}
          signStudent={signStudent}
          err={err}
        />
      </section>
    </>
  );
};

export default SignInMain;
