import React, { useContext, useEffect } from "react";

import { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Context } from "../../../../Context/Context";

import UpdateBlogBody from "./UpdateBlogBody";

import UpperHeader from "../../HeaderComponents/UpperHeader";

import Data from "../../../../Api";
import PhotoData from "../../../../ApiPhoto";
import { Alerts } from "../../../../Context/Alerts";

const UpdateBlogMain = () => {
  const navigate = useNavigate();

  const [user, setUser] = useContext(Context);

  const [alert, setAlert] = useContext(Alerts);

  const [title, setTitle] = useState("");

  const [description, setDescription] = useState(0);

  const [err, setErr] = useState([]);

  const [picture, setPicture] = useState("");

  const [blog, setBlog] = useState("");
  let id = useParams().blogId;

  let data = new Data();

  const findBlog = async () => {
    const blogs = await data.getBlogs();
    const blog = blogs.filter((e) => e.id == id)[0];

    // const regex = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/;
    // regex.test('brwnrqw');

    setBlog(blog);
  };

  useEffect(() => {
    findBlog();
  }, [user]);

  useEffect(() => {
    if (blog) {
      setTitle(blog.title);
      setDescription(blog.description);
    }
  }, [blog]);

  const handlechanger = (title, description, picture) => {
    setTitle(title);
    setDescription(description);
    setPicture(picture);
  };

  const check = () => {
    setErr([]);

    if (title == "") {
      setErr((prev) => {
        return [...prev, "Blog title is required"];
      });
    }

    if (description == "") {
      setErr((prev) => {
        return [...prev, "Blog drescription is required"];
      });
    }
  };

  const updateBlog = async () => {
    check();

    if (err == "") {
      try {
        let data = new Data();

        let photo = new PhotoData();

        let updatedAt = new Date();
        let y = updatedAt.getFullYear();
        let m = updatedAt.getMonth();
        let d = updatedAt.getDate();

        let date = `${y}-${m}-${d}`;

        await data.updateBlog({ id, title, date, description });

        if (picture != "") {
          await photo.uploadBlogPhoto(id, picture);
        }

        setAlert("Blog updated succesfuly");
        navigate("/");
      } catch (e) {
        console.log(e);
      }
    }
  };

  useEffect(() => {
    check();
  }, [title, description]);

  return (
    <section className="form">
      <UpdateBlogBody
        updateBlog={updateBlog}
        handleChanger={handlechanger}
        blog={blog}
        err={err}
      />
    </section>
  );
};

export default UpdateBlogMain;
