import React, { useEffect } from "react";

import { useState } from "react";

import formImage from "../../../../imgs/loginImage.jpg";

import { useNavigate } from "react-router-dom";
const UpdateFormBody = ({ updateC, handleChanger, course, err }) => {
  const navigate = useNavigate();

  const [courseName, setCourseName] = useState(course.courseName);

  const [lectures, setLectures] = useState(course.lectures);

  const [hours, setHours] = useState(course.hours);

  const [totalPrice, setTotalPrice] = useState(course.totalPrice);

  const [perMonth, setPerMonth] = useState(course.perMonth);

  const [minEffort, setMinEffort] = useState(course.minEffort);

  const [maxEffort, setMaxEffort] = useState(course.maxEffort);

  const cancel = () => {
    navigate("/");
  };

  let onChange = (e) => {
    let obj = e.target;
    if (obj.classList.contains("courseName")) {
      setCourseName(obj.value);
    } else if (obj.classList.contains("lectures")) {
      setLectures(obj.value);
    } else if (obj.classList.contains("hours")) {
      setHours(obj.value);
    } else if (obj.classList.contains("totalPrice")) {
      setTotalPrice(obj.value);
    } else if (obj.classList.contains("mounth")) {
      setPerMonth(obj.value);
    } else if (obj.classList.contains("minEffort")) {
      setMinEffort(obj.value);
    } else if (obj.classList.contains("maxEffort")) {
      setMaxEffort(obj.value);
    }
  };

  useEffect(() => {
    handleChanger(
      courseName,
      lectures,
      hours,
      totalPrice,
      perMonth,
      maxEffort,
      minEffort
    );
  });
  return (
    <section className="formBody">
      <div className="inputs" onChange={onChange}>
        <h1>Update your course</h1>

        <div className="input">
          <label>Course Name</label>
          <input
            type="text"
            className="courseName"
            placeholder="Course Name"
            defaultValue={course.courseName}
          ></input>
        </div>

        <div className="input">
          <label>Number of lectures</label>
          <input
            type="number"
            className="lectures"
            placeholder="Number of lectures"
            defaultValue={course.lectures}
          ></input>
        </div>

        <div className="input">
          <label>Total hours</label>
          <input
            type="number"
            className="hours"
            placeholder="Total Hours"
            defaultValue={course.hours}
          ></input>
        </div>

        <div className="input">
          <label>Minimum effort</label>
          <input
            type="number"
            className="minEffort"
            placeholder="Min Effort"
            defaultValue={course.minEffort}
          ></input>
        </div>

        <div className="input">
          <label>Maximum effort</label>
          <input
            type="number"
            className="maxEffort"
            placeholder="Max Effort"
            defaultValue={course.maxEffort}
          ></input>
        </div>

        <div className="input">
          <label>Course total price</label>
          <input
            type="number"
            className="totalPrice"
            placeholder="Total Price"
            defaultValue={course.totalPrice}
          ></input>
        </div>

        <div className="input">
          <label>Per month payment</label>
          <input
            type="number"
            className="mounth"
            placeholder="Price per month"
            defaultValue={course.perMonth}
          ></input>
        </div>

        {err ? (
          err.map((e) => {
            return (
              <p key={e} className="errorMsg">
                {e}
              </p>
            );
          })
        ) : (
          <></>
        )}
        <div className="buttons">
          <button className="btn enroll" onClick={updateC}>
            Update
          </button>
          <button className="btn cancel" onClick={cancel}>
            Cancel
          </button>
        </div>
      </div>
      <div className="formImage">
        <img src={formImage}></img>
      </div>
    </section>
  );
};

export default UpdateFormBody;
