import React, { useEffect } from "react";

import { useState } from "react";

import formImage from "../../../../imgs/loginImage.jpg";

import { useNavigate } from "react-router-dom";

import { FileUploader } from "react-drag-drop-files";

const UpdateBlogBody = ({ updateBlog, handleChanger, blog, err }) => {
  const navigate = useNavigate();

  const [title, setTitle] = useState(blog.title);

  const [description, setDescription] = useState(blog.description);

  const [picture, setPicture] = useState("");

  const fileTypes = ["JPG", "PNG", "GIF"];

  console.log(blog);

  const cancel = () => {
    navigate("/");
  };

  let onChange = (e) => {
    let obj = e.target;
    if (obj.classList.contains("title")) {
      setTitle(obj.value);
    } else if (obj.classList.contains("description")) {
      setDescription(obj.value);
    }
  };

  const handleChange = async (file) => {
    let buufer = await file.arrayBuffer();

    setPicture(buufer);
  };

  useEffect(() => {
    handleChanger(title, description, picture);
  }, [title, description, picture]);
  return (
    <section className="formBody">
      <div className="inputs" onChange={onChange}>
        <h1>Update your blog post</h1>

        <div className="input">
          <input
            type="text"
            className="title"
            placeholder="Blog Title"
            defaultValue={blog.title}
          ></input>
        </div>

        <div className="input">
          <textarea
            className="description"
            placeholder="Blog short description"
            defaultValue={blog.description}
          ></textarea>
        </div>

        <div className="input">
          <h1>Blog picture</h1>
          <FileUploader
            handleChange={handleChange}
            name="file"
            types={fileTypes}
          />
        </div>

        {err.map((e) => {
          return (
            <p key={e} className="errorMsg">
              {e}
            </p>
          );
        })}

        <div className="buttons">
          <button className="btn enroll" onClick={updateBlog}>
            Update
          </button>
          <button className="btn cancel" onClick={cancel}>
            Cancel
          </button>
        </div>
      </div>
      <div className="formImage">
        <img src={formImage}></img>
      </div>
    </section>
  );
};

export default UpdateBlogBody;
