import React, { useContext, useState, useEffect } from "react";

import UpperHeader from "../../HeaderComponents/UpperHeader";
import AddFormBody from "./AddFormBody";
import Data from "../../../../Api";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Context } from "../../../../Context/Context";
import { Alerts } from "../../../../Context/Alerts";

const AddFormMain = () => {
  const navigate = useNavigate();

  const [user, setUser] = useContext(Context);

  const [alert, setAlert] = useContext(Alerts);

  const [logedUser, setLogged] = useState("");

  const [courseName, setCourseName] = useState("");

  const [lectures, setLectures] = useState(0);

  const [hours, setHours] = useState(0);

  const [totalPrice, setTotalPrice] = useState(0);

  const [perMonth, setPerMonth] = useState(0);

  const [creatorFirstName, setCreatorFirst] = useState("");

  const [creatorLastName, setCreatorLast] = useState("");

  const [creatorId, setId] = useState(0);

  const [minEffort, setMin] = useState(0);

  const [maxEffort, setMax] = useState(0);

  const [category, setCategory] = useState("");

  const [creatorPicture, setCreatorPicture] = useState("");

  const [err, setErr] = useState([]);

  const Api = new Data();

  const userDetails = async () => {
    const users = await Api.getUsers();

    if (typeof user == "object") {
      const logUser = users.filter((e) => e.id == user.id)[0];

      setLogged(logUser);
    }
  };

  useEffect(() => {
    userDetails();
  }, [user]);

  useEffect(() => {
    if (typeof logedUser == "object") {
      setCreatorFirst(logedUser.firstName);
      setCreatorLast(logedUser.lastName);
      setCreatorPicture(logedUser.picture.data);
      setId(logedUser.id);
    }
  });

  const check = () => {
    setErr([]);

    if (courseName == "") {
      setErr((prev) => {
        return [...prev, "Course name is required"];
      });
    }

    if (lectures == "") {
      setErr((prev) => {
        return [...prev, "Lectures are required"];
      });
    } else if (lectures < 5) {
      setErr((prev) => {
        return [...prev, "You cant have a course with less then 5 lectures !"];
      });
    } else if (lectures > 100) {
      setErr((prev) => {
        return [...prev, "100 is the max number of lectures for a course"];
      });
    }

    if (hours == "") {
      setErr((prev) => {
        return [...prev, " Total number of hours is required"];
      });
    } else if (hours > 500) {
      setErr((prev) => {
        return [...prev, " Total number of hours  cant be higher then 500"];
      });
    }

    if (totalPrice == "") {
      setErr((prev) => {
        return [...prev, "Specify the price of the course"];
      });
    }

    if (perMonth == "") {
      setErr((prev) => {
        return [...prev, "You need to provide per month payment"];
      });
    } else if (perMonth > totalPrice / 2) {
      setErr((prev) => {
        return [...prev, "You cant have less then 2 moths time payment"];
      });
    }

    if (minEffort == "") {
      setErr((prev) => {
        return [
          ...prev,
          "Specify the minimum effort for the course (hours per week)",
        ];
      });
    } else if (minEffort > maxEffort) {
      setErr((prev) => {
        return [...prev, "Minimum effort can't be higher then maximum effort"];
      });
    }

    if (maxEffort == "") {
      setErr((prev) => {
        return [...prev, "Number of total hours is requaired"];
      });
    } else if (maxEffort > hours * 1) {
      setErr((prev) => {
        return [
          ...prev,
          "You cant have maximum effort higher then total hours",
        ];
      });
    } else if (maxEffort > 40) {
      setErr((prev) => {
        return [...prev, "You cant have more then 40 hours per week"];
      });
    }
  };

  const handlechanger = (
    courseName,
    lectures,
    hours,
    totalPrice,
    perMonth,
    minEffort,
    maxEffort,
    category
  ) => {
    setCourseName(courseName);
    setLectures(lectures);
    setHours(hours);
    setTotalPrice(totalPrice);
    setPerMonth(perMonth);
    setMin(minEffort);
    setMax(maxEffort);
    setCategory(category);
  };

  const addCourse = async () => {
    check();

    if (err == "") {
      try {
        let data = new Data();

        let response = await data.addCourse({
          courseName,
          lectures,
          creatorId,
          creatorFirstName,
          creatorLastName,
          hours,
          totalPrice,
          perMonth,
          minEffort,
          maxEffort,
          category,
          creatorPicture,
        });

        if (response.status == 204) {
          navigate("/");
          setAlert("Course created succesfully");
        } else {
          setAlert("Completati toate campurile");
        }
      } catch (e) {
        throw new Error(e);
      }
    }
  };

  useEffect(() => {
    check();
  }, [courseName, hours, minEffort, maxEffort, lectures, totalPrice, perMonth]);

  return (
    <section className="form">
      <AddFormBody handlechanger={handlechanger} add={addCourse} err={err} />
    </section>
  );
};

export default AddFormMain;
