import React, { useContext } from "react";

import { useState, useEffect } from "react";

import formImage from "../../../../imgs/loginImage.jpg";

import { useNavigate } from "react-router-dom";

import { FileUploader } from "react-drag-drop-files";
import { Alerts } from "../../../../Context/Alerts";
import Alert from "react-bootstrap/Alert";

const SingInformBody = ({ handlechanger, signStudent, err }) => {
  const navigate = useNavigate();

  const [alert, setAlert] = useContext(Alerts);

  const [firstName, setFirst] = useState("");

  const [lastName, setLast] = useState("");

  const [email, setEmail] = useState("");

  const [age, setAge] = useState(0);

  const [password, setPassword] = useState("");

  const [confirmPassword, setConfirmPassword] = useState("");

  const [role_id, setRole] = useState(0);

  const [picture, setPicture] = useState("");

  const fileTypes = ["JPG", "PNG", "GIF"];

  useEffect(() => {
    setAlert(err);
  }, [err]);

  const cancel = () => {
    navigate("/LogIn");
    setAlert([]);
  };

  const onChange = (e) => {
    let obj = e.target;

    if (obj.classList.contains("firstName")) {
      setFirst(obj.value);
    } else if (obj.classList.contains("lastName")) {
      setLast(obj.value);
    } else if (obj.classList.contains("email")) {
      setEmail(obj.value);
    } else if (obj.classList.contains("age")) {
      setAge(obj.value);
    } else if (obj.classList.contains("password")) {
      setPassword(obj.value);
    } else if (obj.classList.contains("type")) {
      if (obj.value == "Student") {
        setRole(1);
      } else if (obj.value == "Profesor") {
        setRole(2);
      }
    } else if (obj.classList.contains("ConfirmPassword")) {
      setConfirmPassword(obj.value);
    }
  };

  const handleChange = async (file) => {
    let buufer = await file.arrayBuffer();

    setPicture(buufer);

    console.log(picture);
  };

  useEffect(() => {
    handlechanger(
      firstName,
      lastName,
      email,
      age,
      password,
      confirmPassword,
      role_id,
      picture
    );
  });

  return (
    <>
      <section className="formBody">
        <div className="inputs" onChange={onChange}>
          <h1>Create a free account</h1>
          <div className="input">
            <select className="type" defaultValue="">
              <option value="" disabled>
                Chose account type
              </option>
              <option>Student</option>
              <option>Profesor</option>
            </select>
          </div>

          <div className="input">
            <input
              type="text"
              className="firstName"
              placeholder="First Name"
            ></input>
          </div>

          <div className="input">
            <input
              type="text"
              className="lastName"
              placeholder="Last Name"
            ></input>
          </div>

          <div className="input">
            <input type="email" className="email" placeholder="Email"></input>
          </div>

          <div className="input">
            <input type="number" className="age" placeholder="Age"></input>
          </div>

          <div className="input">
            <input
              type="password"
              className="password"
              placeholder="Password"
            ></input>
          </div>

          <div className="input">
            <input
              type="password"
              className="ConfirmPassword"
              placeholder="Confirm Password"
            ></input>
          </div>

          <div className="input">
            <h1>Profile pic (optional)</h1>
            <FileUploader
              handleChange={handleChange}
              name="file"
              types={fileTypes}
            />
          </div>

          {err.map((e) => {
            return (
              <Alert variant="danger" style={{ width: "42rem" }} key={e}>
                <Alert.Heading>{e}</Alert.Heading>
              </Alert>
            );
          })}

          <div className="buttons">
            <button className="btn enroll" onClick={signStudent}>
              Create Account
            </button>
            <button className="btn cancel" onClick={cancel}>
              Cancel
            </button>
          </div>
        </div>

        <div className="formImage">
          <img src={formImage}></img>
        </div>
      </section>
    </>
  );
};

export default SingInformBody;
